<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="addService">新增服务产品</el-button>
            </div>
            <div>
                <el-input
                    placeholder="请输入套餐名称"
                    size="small"
                    style="width: 140px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="服务类型"
                           style="width: 140px">
                    <el-option
                        v-for="(v,k) in $Cfg.postType"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="服务规格"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small" @click="refresh = !refresh">重置</el-button>
            </div>
        </ToolBar>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="date"
                label="ID"
                width="180">
            </el-table-column>
            <el-table-column
                prop="name"
                label="服务产品"
                width="180">
            </el-table-column>
            <el-table-column
                prop="address"
                label="服务规格">
            </el-table-column>
            <el-table-column
                prop="address"
                label="总服务金额">
            </el-table-column>
            <el-table-column
                prop="address"
                label="服务标签">
            </el-table-column>
            <el-table-column
                prop="address"
                label="指定工作室">
            </el-table-column>
            <el-table-column
                prop="address"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建时间">
            </el-table-column>
            <el-table-column
                prop="date"
                label="创建时间">
            </el-table-column>

            <el-table-column
                label="操作"
                width="210">
                <template slot-scope="scope">
                    <el-button @click="enable(scope.row)" v-if="scope.row.isOpen" type="success" size="small"
                    >启用
                    </el-button>
                    <el-button @click="forbidden(scope.row)" v-else type="danger" size="small"
                    >禁用
                    </el-button>
                    <el-button @click="previwService(scope.row)" type="info" size="small"
                    >预览
                    </el-button>
                    <el-button @click="editService(scope.row)" type="primary" size="small"
                    >修改
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    isOpen: true,
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    isOpen: false,
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            addService() {
                this.$router.push('/service_add_3')
            },
            editService(row) {
                this.$router.push('/service_edit_3')
            },
            previwService(row) {
                this.$router.push('/service_preview_3')
            },
            forbidden(row) {
                this.$confirm('确定禁用该服务产品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '禁用该服务产品成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消禁用该服务产品'
                    });
                });
            },
            enable(row) {
                this.$confirm('确定启用该服务产品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '启用成功该服务产品!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用该服务产品'
                    });
                });
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
